.resetButton {
    border-radius: 5px;
    height: 35px;
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    border: none;
    background-color: hsl(172, 67%, 45%);
    color: hsl(183, 100%, 15%);
    margin-top: 60px;
  }
  
  .resetButton:disabled {
    background-color: hsl(183, 100%, 23%);
  }